.md-backdrop {
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, .2);
  transition: opacity .5s cubic-bezier(.4, 0, .2, 1), visibility .5s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
}

.md-is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

.md-backdrop.md-is-hidden .modal-loc {
  transform: translate(0%)scale(0);
}

.modal-loc {
  width: 280px;
  min-height: 365px;
  background-color: #fcfcfc;
  border: 2px solid #f8d24e;
  border-radius: 10px;
  padding: 30px;
  transition: transform .5s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%)scale(1);
}

@media screen and (min-width: 1200px) {
  .modal-loc {
    width: 568px;
    height: 458px;
    padding: 30px 60px;
  }
}

